import * as React from 'react';
import Item from './Item';

const itemsArr = [
    { id: 1, name: 'Шапка', count: 1 },
    { id: 2, name: 'Колан', count: 2 },
    { id: 3, name: 'Дънки', count: 3 },
];

function Order() {
    const [items, setItems] = React.useState(itemsArr);

    const removeItem = (name) => {
        const updatedItems = items.filter((item) => item.name !== name);
        setItems(updatedItems);
    }

    const updateItem = (item) => {
        const updatedItems = items.map((i) => {
            if (item.id === i.id) return item;
            return i;
        });

        setItems(updatedItems);
    }

    return (
        items.map((item) => <Item key={item.id} item={item} removeItem={removeItem} updateItem={updateItem} />)
    );
}

export default Order;
