import * as React from 'react';
import { Link } from 'react-router-dom';

function Navigation() {
    return (
        <div style={{backgroundColor: '#ccc'}}>
            <div className="container mb-3">
                <div className="row">
                    <div className="col-12">
                        <Link className="mr-2" to="/">Home</Link>
                        <Link className="mx-2" to="/contacts">Contacts</Link>
                        <Link className="mx-2" to="/products">Products</Link>
                        <Link className="mx-2" to="/order">Order</Link>
                        <Link className="mx-2" to="/addNewQuestion">Add New Question</Link>
                        <Link className="ml-2" to="/quiz">Quiz</Link>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Navigation;
