import * as React from 'react';

const AddNewQuestion = () => {
    const question = React.useRef();
    const answer = React.useRef();
    const extended = React.useRef();
    const deleteInput = React.useRef();

    const addNewQuestion = (e) => {
        e.preventDefault();
        const questionObj = {
            question: question.current.value,
            answer: answer.current.value,
            extendedQuestion: extended.current.value,
        };

        fetch('http://localhost:8000/questions', {
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            method: "POST",
            body: JSON.stringify(questionObj),
        });
    };

    const deleteQuestion = (e) => {
        e.preventDefault();
        const id = deleteInput.current.value;

        fetch(`http://localhost:8000/questions/${id}`, { method: 'DELETE' });
    }

    return (
        <div>
            <form onSubmit={addNewQuestion}>
                <input type="text" ref={question} />
                <input type="text" ref={answer} />
                <input type="text" ref={extended} />
                <button type="submit">Submit</button>
            </form>
            <form onSubmit={deleteQuestion}>
                <input type="text" ref={deleteInput} />
                <button type="submit">Submit</button>
            </form>
        </div>
    );
};

export default AddNewQuestion;
