import * as React from 'react';

const products = [
  { name: 'Шапка', count: 10, price: 10 },
  { name: 'Колан', count: 20, price: 20 },
  { name: 'Дънки', count: 30, price: 50 },
];

function Products() {
  return (
    <div>
      {products.map((pr) => {
        return (
          <div key={pr.name}>
            {pr.name}: {pr.price}$, останали бройки: {pr.count}
          </div>
        )
      })}
    </div>
  );
}

export default Products;
