import * as React from 'react';
import questionImg from '../images/question.png';
import smileyImg from '../images/smiley.jpg';
import sadImg from '../images/sad.jpg';

const Quiz = () => {
    const [currentQuestion, setCurrentQuestion] = React.useState(0);
    const [showJoker, setShowJoker] = React.useState(false);
    const [hasUsedJoker, setHasUsedJoker] = React.useState(false);

    const [loading, setLoading] = React.useState(false);

    const [questions, setQuestions] = React.useState([]);
    const counter = React.useRef(0);

    const questionRef = React.useRef();
    const inputRef = React.useRef();
    const statusRef = React.useRef();
    const btnRef = React.useRef();

    React.useEffect(() => {
        setLoading(true);
        fetch('http://localhost:8000/questions')
            .then((response) => response.json())
            .then((questions) => setQuestions(questions))
            .then(() => setLoading(false))
            .catch(() => setLoading(false));
    }, []);

    React.useEffect(() => {
        if (questions && questions.length && questionRef.current) {
            questionRef.current.innerHTML = questions[currentQuestion].question;
        }
    }, [currentQuestion, questions, loading]);

    const checkAnswer = () => {
        const isCorrect = inputRef.current.value === questions[currentQuestion].answer;
        statusRef.current.innerHTML = isCorrect 
            ? `<img src="${smileyImg}" width="50"/><span>Правилен отговор</span>`
            : `<img src="${sadImg}" width="50"/><span>Грешен отговор</span>`;

        if (!isCorrect) counter.current++;

        if (counter.current >= 3) {
            statusRef.current.innerHTML += ', Искате ли жокер?';
            setShowJoker(true);
        }
    };

    const clearStatus = () => {
        statusRef.current.innerHTML = '';
    };

    const nextQuestion = () => {
        const index = currentQuestion === (questions.length - 1) ? 0 : currentQuestion + 1;
        setCurrentQuestion(index);
        setShowJoker(false);
        clearStatus();
    };

    const useJoker = () => {
        setHasUsedJoker(true);
        questionRef.current.innerHTML = questions[currentQuestion].extendedQuestion;
    };

    if (loading) return <h1>Loading...</h1>;

    return (
        <React.Fragment>
            <img src={questionImg} width='50' alt="img" />
            <div ref={questionRef}></div>
            <input type="text" ref={inputRef} />
            <div ref={statusRef}></div>
            <button onClick={checkAnswer} ref={btnRef}>Отговори</button>
            <button onClick={clearStatus}>Изчисти статус</button>
            <button onClick={nextQuestion}>Следващ</button>
            {(showJoker || hasUsedJoker)
                && <button onClick={useJoker}>Жокер</button>}
        </React.Fragment>
    )
};

export default Quiz;
